
.ranking-table {
    padding:12px;
    font-family: 'Segoe UI', sans-serif;
    background-color: #fffef9;
    max-width: 800px;
    margin: 0;
    margin-left: 0;
    margin-top: 0px;
    overflow-x: auto;
  }
  
  .ranking-table h2 {
    margin-bottom: 16px;
    color: #4e342e;
    font-size: 1.25rem;
    font-weight: bold;
  }
  
  .ranking-table table {
    width: 95%;
    border-collapse: collapse;
    background-color: #fefefe;
    display: block;
    max-height: 400px; 
    overflow-y: auto;
  }

  .ranking-table thead {
    position: sticky;
    top: 0;
    background-color: #d7ccc8;
    z-index: 1;
  }
  
.ranking-table th,
.ranking-table td {
    border: 1px solid #d7ccc8;
    padding: 12px;
    text-align: left;
    min-width: 50px;
    max-width: 70px;
    width: 10%;
  }

.ranking-table td:nth-child(2),
.ranking-table th:nth-child(2) {
    min-width: 90px;
    max-width: 110px;
    width: 20%;
  }

  .ranking-table th {
    background-color: #d7ccc8;
    color: #3e2723;
    cursor: pointer;
  }

.table-wrapper {
    max-height: 400px;
    overflow-y: auto;
    overflow-x: auto;
  }

.ranking-table thead th {
    position: sticky;
    top: 0;
    background-color: #d7ccc8;
    z-index: 2;
  }

  .sticky-col {
    position: sticky;
    left: 0;
    background-color: white; 
    text-align: center;
    font-weight: normal;
    min-width: 60px;
    max-width: 60px;
    width: 60px;
    border-right: 1px solid #d7ccc8;
  }

.ranking-table td:nth-last-child(-n+3),
.ranking-table th:nth-last-child(-n+3) {
  min-width: 100px;
  max-width: 140px;
  width: 15%;
}